.subpage {
  z-index: 5;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: white;
  margin: 0 auto;
  background-image: url("icons/fcc_bg.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position-x: center; }
  .subpage .page {
    position: relative;
    scroll-behavior: smooth;
    overflow-x: hidden;
    overflow-y: scroll;
    width: 100%;
    height: 100vh; }

.page .content {
  width: 45em;
  max-width: 90%;
  margin: 3em auto 8em auto; }
  .page .content .links a + a {
    margin-left: 1em; }
  .page .content img {
    max-width: 100%;
    max-height: 50vh;
    display: block;
    margin: 4em auto; }
    @media screen and (min-width: 1200px) and (min-aspect-ratio: 13 / 10) {
      .page .content img {
        margin: 6em auto; } }
    .page .content img.sm-bottom-margin {
      margin-bottom: 1em; }
  .page .content .attention-box {
    border-color: #d40f41;
    border-width: 2px;
    border-style: solid;
    margin: 4em auto;
    padding: 1em; }
    .page .content .attention-box h3 {
      display: block;
      text-transform: uppercase;
      text-align: center;
      margin: 0;
      margin-bottom: 1em; }
    @media screen and (min-width: 1200px) and (min-aspect-ratio: 13 / 10) {
      .page .content .attention-box {
        max-width: 30em;
        margin: 6em auto; } }
  .page .content .emphasis {
    font-style: italic;
    font-weight: bold;
    color: gray;
    font-size: 1.5em;
    display: block;
    margin: 0 auto;
    text-align: center;
    margin: 4em auto; }
    .page .content .emphasis .bigger {
      font-size: 1.5em;
      color: #d40f41; }
  .page .content .two-content {
    margin: 4em auto; }
    @media screen and (min-width: 1200px) and (min-aspect-ratio: 13 / 10) {
      .page .content .two-content {
        margin: 6em auto; } }
    @media screen and (min-width: 800px) {
      .page .content .two-content {
        display: flex;
        justify-content: space-between;
        align-items: flex-start; }
        .page .content .two-content.reverse {
          flex-direction: row-reverse; }
        .page .content .two-content h1, .page .content .two-content h2, .page .content .two-content h3 {
          margin-top: 0; }
        .page .content .two-content img {
          margin: 0; }
        .page .content .two-content > * {
          max-width: 48%; } }

@media screen and (min-width: 1200px) and (min-aspect-ratio: 13 / 10) {
  .page .content {
    margin-top: 3em;
    position: relative;
    right: 10%; } }

html {
  margin: 0; }

body {
  margin: 0;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 18px;
  position: relative;
  scroll-behavior: smooth;
  height: 100vh;
  overflow-x: hidden;
  overflow-y: scroll; }
  @media screen and (min-width: 2048px) {
    body {
      font-size: 1.5em; } }
  @media screen and (min-width: 2560px) {
    body {
      font-size: 1.8em; } }
  @media screen and (min-width: 3840px) {
    body {
      font-size: 3em; } }
  @media screen and (min-width: 5000px) {
    body {
      font-size: 4em; } }
  @media screen and (min-width: 6000px) {
    body {
      font-size: 5em; } }
  @media screen and (min-width: 7680px) {
    body {
      font-size: 6em; } }

h1 {
  color: #d40f41; }

h2 {
  color: #696969;
  font-size: 2em; }

h1, h2 {
  margin-top: 2em; }

.block {
  display: block;
  margin: .5em auto; }

.mt {
  margin-top: 1em; }

.mt2 {
  margin-top: 2em; }

a {
  color: #d40f41; }
  a:visited {
    color: #d40f41; }
  a:hover {
    color: red; }

button {
  display: block;
  margin: .5em auto;
  background-color: #005EB8;
  color: white;
  font-size: 1em;
  box-shadow: none;
  border: none;
  padding: .5em;
  min-width: 8em;
  cursor: pointer;
  border-radius: .1em;
  border-color: #3da1ff;
  border-style: solid;
  border-width: 1px; }
  button:hover {
    filter: brightness(1.3); }

.centered {
  text-align: center; }

.small {
  font-size: .8em;
  margin: 0; }

.margin-border {
  margin: 6em 1em 1em 1em; }
  @media screen and (min-width: 500px) {
    .margin-border {
      margin: 1em; } }
